.app__container {
  max-width: 1200px;
  margin: 0 auto;
}
.app__container--enter-active {
  -webkit-animation: routerSlideIn .3s ease-out forwards;
          animation: routerSlideIn .3s ease-out forwards;
}
.app__container-leave-active {
  -webkit-animation: routerSlideOut .3s ease-out forwards;
          animation: routerSlideOut .3s ease-out forwards;
}
@-webkit-keyframes routerSlideIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes routerSlideIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes routerSlideOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes routerSlideOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}

