.array-sorting__movies {
  border: 1px solid #eee;
}
.array-sorting__movies tbody tr:nth-child(odd) {
    background-color: #eee;
}
.array-sorting__movies tr.is-highest td {
    color: green;
}
.array-sorting__movies tr.is-lowest td {
    color: red;
}
.array-sorting__movies th,
  .array-sorting__movies td {
    font-family: "Roboto", Arial, sans-serif;
    padding: 20px;
}
.array-sorting__movies th {
    font-weight: 700;
    font-size: 32px;
    color: black;
    text-align: left;
    cursor: pointer;
    -webkit-transition: .3s color;
    transition: .3s color;
}
.array-sorting__movies th span::after {
      position: relative;
      top: 0px;
      left: 15px;
      display: inline-block;
      visibility: hidden;
      width: 0px;
      height: 0px;
      overflow: hidden;
      margin-right: 10px;
      content: '';
      border: 10px;
      border-color: transparent;
      border-width: 10px;
      border-style: solid;
}
.array-sorting__movies th.is-active span::after {
      visibility: visible;
}
.array-sorting__movies th.sorted-asc span::after {
      top: 5px;
      border-top-color: deepskyblue;
}
.array-sorting__movies th.sorted-desc span::after {
      top: -5px;
      border-bottom-color: deepskyblue;
}
.array-sorting__movies th:hover, .array-sorting__movies th.is-active {
      color: deepskyblue;
}
.array-sorting__movies td {
    font-size: 22px;
}

